import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';

export default {
	data() {
		return {
			name: '', //用户名
			image: '', //头像
			token: '',
			externalId: '',
			buyerNick: '',
			isIphoneX:false, //x的兼容
		}
	},
	methods: {
		//单位转化方法
		calUpx(num) {
		    return uni.upx2px(num) + 'px';
		},
		tohome() {
			uni.reLaunch({
				url: '../index/index',
			}); 
		},
		tocar() {
			uni.navigateToMiniProgram({
				appId: '2018122862752160',
				extraData: {},
				success(res) {
					// 打开成功
				}
			})
		},
		handlePay() {
			let externalId = '';
			let orderNo = '';
			try{
				const value = uni.getStorageSync('externalId')
				if(value) {
					externalId = value;
					
					// 下单 得到 outerId 支付宝交易号
					ztRequest.post('/api/public/alipay/trade', {
						productCode: 'ne7ilNpCIN7Vlpph',
						amount: "0.01",
						comeDate: "2050-12-12",
						count: "1",
						externalId: externalId,
						merchantCode: "b49c90557893a9f8ed229df13305804a",
						payment: "1",
						userCode: "23027b4f3bd5558cfb462873dff5149b"
					}).then(res => {
						if(res.data.code == '000000') {
							orderNo = res.data.content.outerId;
					

							// 唤起支付
							my.tradePay({
								tradeNO: orderNo,
								success: (re) => {
									
								},
								fail: (re) => {
									
								}
							});

							
						} else {
							uni.showToast({
								title: res.data.message
							})
						}
					}).catch(err => {
						console.log(err)
					})
				} else {
					// 授权操作
					
					
				}
			}catch(e){
				//TODO handle the exception
				console.log(e)
			}
			
			


		},
		loginIn() {
			ztRequest.post('/api/public/user/login', {
				externalId: this.externalId,
				buyerNick: this.name,
				imgUrl:this.image
			}).then(res => {
				if(res.data.code == '000000') {
					uni.setStorage({
						key: 'openId',
						data: res.data.content.openid,
					});
					uni.setStorage({
						key: 'externalId',
						data: res.data.content.externalId,
					});
					uni.setStorage({
						key: 'token',
						data: res.data.content.token,  
					});
					
				}else {
					uni.showToast({
						title:res.data.message
					})
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
	onLoad() {
		let _this = this;
		
		uni.getSystemInfo({
			success: res => {
				// console.log('手机信息res'+res.model)
				let modelmes = res.model;
				if (modelmes.search('iPhone X') != -1) {
					_this.isIphoneX = true;
					// _this.btuBottom="68rpx";
					// console.log(_this.isIphoneX,_this.btuBottom,123456)
				}
			},
		})

		my.getAuthCode({
			scopes: 'auth_user',
			success: (res) => {
				if (res.authCode) {
					// console.log(res,2222);
					uni.getUserInfo({
						provider: 'alipay',
						success: infoRes => {
							uniRequest.post('/user/userlogin', {
								authorization_code: res.authCode,
								nickname: infoRes.userInfo.nickName,
								avatar: infoRes.userInfo.avatarUrl
							}).then(function(res) {
								console.log(res, 'res');
								
							}).catch(function(error) {
								console.log(error);
							});
							
						
							this.name = infoRes.userInfo.nickName;
							this.image = infoRes.userInfo.avatarUrl;

						}
					});

					// 服务端 换取用户id 
					ztRequest.post('/api/public/alipay/auth', {
						authcode: res.authCode
					}).then(response => {
						if (response.data.code == '000000') {

							this.externalId = response.data.content;

							// 服务端 登录
							this.loginIn()

						} else {
							uni.showToast({
								title: res.data.message
							})
						}
					}).catch(err => {
						console.log(err)
					})

				}
			},
		});


	}
}
